.Button {
    position: relative;
    display: inline-block;
    padding: 12px 38px;
    color: white;
    font-weight: bold;
    font-size: 16px;
    line-height: 18px;
    background-color: rgb(142, 167, 255);
    border: 0px;
    border-radius: 10px;
    cursor: pointer;
    transition: box-shadow 0.2s linear 0s;
    margin-top: 20px;
    &:hover {
        background-color: rgb(110, 140, 248);
    }
}